  padding: 0;
}

html {
  width: 100%;
  min-width: 100% !important;
  margin: 0;
  min-height: 100vh;
  padding: 0;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  position: absolute;
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-size: 1.2rem;
  width: 100%;
  min-width: 100% !important;
  min-height: 100vh;
  padding: 0;
}

