  /*top: 3.5rem;*/
}

button {
  border: none;
}

button:hover,
input[type="submit"] {
  cursor: pointer;
}

/* note: try adding the loader for the page & the list */
button:focus {
  outline: none;
  box-shadow: 0px 0px 0px 0.5px rgba(255, 255, 255, 1),
    0px 0px 0px 1.5px rgba(0, 0, 0, 1), 0px 0px 0px 2.5px rgba(255, 255, 255, 1),
