  /* box-shadow: 0px 0px 0.5px 1px #ffffff, 0px 0px 1px 2px #969696, */
  /* 0px 0px 2px 3px #000000, 0 0 3px 4px #fff; */
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
  box-shadow: inset 0px 0px 0px 0.5px rgba(255, 255, 255, 1),
    inset 0px 0px 0px 1.5px rgba(0, 0, 0, 1),
    inset 0px 0px 0px 2.5px rgba(255, 255, 255, 1),
    inset 0px 0px 0px 3px rgba(0, 0, 0, 1);
}

input[type="checkbox"]:hover {
  cursor: pointer;
}

input[type="date"]::-webkit-inner-spin-button,
input[type="time"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  display: none;
}

input[type="date"]::-webkit-calendar-picker-indicator,
input[type="time"]::-webkit-calendar-picker-indicator {
  -webkit-appearance: none;
  display: none;
}

a {
