.error-notification-container {
  display: flex;
  border: 1px solid #f777;
  background-color: #fddd;
  color: #733;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 3rem;
  border-radius: 5px;

  padding: 0 0.5rem;
  -webkit-transition: opacity 0.3s ease, visibility 0.01s ease 0.29s,
    min-height 0.01s ease 0.29s, height 0.01s ease 0.29s;
  transition: opacity 0.3s ease, visibility 0.01s ease 0.29s,
    min-height 0.01s ease 0.29s, height 0.01s ease 0.29s;
}

.error-notification-container.hide {
  visibility: hidden;
  min-height: 0;
  height: 0;
  opacity: 0;
}

